// @flow
import React, { lazy, FC, memo } from "react";
import { Flex, Image } from "@chakra-ui/react";
import iconsImages from "../../constants/icons-images";
import { useNavigate } from "react-router-dom";
import Container from "../Container";
import { useSelector } from "react-redux";
import { AuthReducerProps } from "../../interfaces";
import { RootState } from "../../redux/store";
import { GLOBAL_PREFIX } from "../../constants";

interface TopIconBarProps {
  pos?: any;
}

const TopIconBar: FC<TopIconBarProps> = ({ pos }): JSX.Element => {
  let navigate = useNavigate();

  const { token }: AuthReducerProps = useSelector(
    (state: RootState) => state.authReducer
  );

  const { styles, img } = useStyles();
  return (
    <Container>
      <Flex
        as={"button"}
        onClick={() => {
          token
            ? navigate(`${GLOBAL_PREFIX}/dashboard`)
            : navigate(`${GLOBAL_PREFIX}/`);
        }}
        position={pos || "absolute"}
        {...styles}
      >
        <Image
          // as="button"
          data-testid={"logo-1"}
          src={iconsImages.appLogo}
          {...img}
        />
      </Flex>
    </Container>
  );
};

export default memo(TopIconBar);

const useStyles = () => {
  return {
    styles: {
      w: "100%",
      h: "50px",
      justifyContent: "flex-start",
    },
    img: {
      mt: 5,
      // w: "80px",
      h: "50px",
    },
  };
};
